  // 排序
  if (sortOption.value) {
    const [field, order] = sortOption.value.split('_');
    result.sort((a, b) => {
      if (order === 'asc') {
        return a[field] > b[field] ? 1 : -1;
      } else {
        return a[field] < b[field] ? 1 : -1;
      }
    });
  }
  
  return result;
});

// 方法
const handleSearch = () => {
  currentPage.value = 1;
};

const handleFilter = () => {
  currentPage.value = 1;
};

const handleSort = () => {
  currentPage.value = 1;
};

const handleSizeChange = (val) => {
  pageSize.value = val;
  currentPage.value = 1;
};

const handleCurrentChange = (val) => {
  currentPage.value = val;
};

const getStatusType = (status) => {
  const statusMap = {
    'online': 'success',
    'offline': 'danger',
    'maintenance': 'warning',
    'low_battery': 'info'
  };
  return statusMap[status] || 'default';
};

const getStatusText = (status) => {
  const statusMap = {
    'online': '在线',
    'offline': '离线',
    'maintenance': '维修中',
    'low_battery': '低电量'
  };
  return statusMap[status] || '未知';
};

const getBatteryColor = (battery) => {
  if (battery > 70) return '#67c23a';
  if (battery > 30) return '#e6a23c';
  return '#f56c6c';
};

const handleRowClick = (row) => {
  viewVehicleDetails(row);
};

const viewVehicleDetails = (vehicle) => {
  selectedVehicle.value = { ...vehicle };
  vehicleDialogVisible.value = true;
};

const editVehicle = (vehicle) => {
  ElMessage({
    type: 'info',
    message: `编辑车辆 ${vehicle.id} 的信息`,
    duration: 2000
  });
  // 这里可以打开编辑对话框
};

const maintainVehicle = (vehicle) => {
  ElMessageBox.confirm(
    `确定将车辆 ${vehicle.id} 转入维护状态吗？`,
    '维护确认',
    {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }
  ).then(() => {
    ElMessage({
      type: 'success',
      message: `车辆 ${vehicle.id} 已转入维护状态`,
      duration: 2000
    });
  }).catch(() => {});
};

const disableVehicle = (vehicle) => {
  ElMessageBox.confirm(
    `确定禁用车辆 ${vehicle.id} 吗？禁用后，用户将无法租用该车辆。`,
    '禁用确认',
    {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'danger'
    }
  ).then(() => {
    ElMessage({
      type: 'success',
      message: `车辆 ${vehicle.id} 已禁用`,
      duration: 2000
    });
    vehicleDialogVisible.value = false;
  }).catch(() => {});
};

const addNewVehicle = () => {
  ElMessage({
    type: 'info',
    message: '添加新车辆',
    duration: 2000
  });
  // 这里可以打开添加车辆的对话框
};

onMounted(() => {
